/* Skills.css */
.skills {
    padding: 50px 20px;
    background-color: #f4f4f4;
  }
  
  .skills-content {
    display: flex;
    align-items: flex-start;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .skills-description {
    flex: 1;
    padding-right: 40px;
  }
  
  .skills-description h2 {
    font-size: 36px;
    margin-bottom: 20px;
    color: #333;
  }
  
  .skills-description p {
    font-size: 18px;
    line-height: 1.6;
    color: #666;
  }
  
  .skills-cards {
    flex: 2;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  
  .skill-card {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .skill-card img {
    max-width: 100px;
    margin-bottom: 20px;
  }
  
  .skill-card h3 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #333;
  }
  
  .skill-card p {
    font-size: 16px;
    color: #666;
  }
  
